require('../styles/articleList.less');
var React = require('react');
var $ = require('jquery');
var marked = require('marked');

var loadingGif = require('../images/loading_spinner.gif');

var Article = React.createClass({
  rawMarkup: function (data) {
    var rawMarkup = marked(data.toString(), {sanitize: true});
    return {__html: rawMarkup};
  },
  render: function () {
    return (
      <div className="articles">
        <div className="up-part">
          <a href={this.props.data.author.homepage}><img src={this.props.data.author.avatar}/></a>
          <a href={this.props.data.link} className="title">{this.props.data.title}&nbsp;&nbsp;<span
            className="nickname">{this.props.data.author.nickname}</span></a>
        </div>
        <a href={this.props.data.link} className="down-part">
          <div className="desc">{this.props.data.desc}</div>
        </a>
      </div>
    )
  }
});

var ArticleList = React.createClass({
  getArticles: function (params, callback) {
    $('#J_loading').show();
    $.ajax({
      url: 'article-get/divio',
      data: {
        _p: params.position,
        _l: params.limit
      },
      success: function (data) {
        if (data && data.length > 0) {
          callback(data);
        } else {
          $('#J_loading').hide();
          $(document).off('scroll');
        }
      },
      error: function () {
        alert('error');
      }
    });
  },
  getInitialState: function () {
    return {articles: []};
  },
  componentDidMount: function () {
    var self = this;
    self.getArticles({
      position: 0,
      limit: 10
    }, function (data) {
      self.setState({
        articles: data
      });
    });
    $(document).on('scroll', function () {
      // scroll
      if ($(document).height() - ($(window).height() + $(document).scrollTop()) === 0) {
        self.getArticles({
          position: self.state.articles.length,
          limit: 10
        }, function (data) {
          self.setState({
            articles: self.state.articles.concat(data)
          });
        });
      }
    })
  },
  render: function () {
    var articleNodes = this.state.articles.map(function (item) {
      return (
        <Article data={item} key={item.link}>
        </Article>
      );
    });
    return (
      <div className="articleList">
        {articleNodes}
        <div id="J_loading" className="loading">
          <img src={loadingGif} alt=""/>
        </div>
      </div>
    );
  }
});

module.exports = ArticleList;



